import React, { Component } from 'react';
import './App.css';

import { FaceExtractor } from './models/FaceExtractor'
import { EmotionNet } from './models/Model'
import { getImg, drawBox, nextFrame, drawText } from './util'

import { MOBILE_DEVICE_WIDTH } from './constants';
class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      ready: false,
      detections: [],
      
    }
    this.isForwarding = false;
    this.stream = null;
    this.video = null;
    this.canvas = null;
    this.MOBILE_DEVICE_WIDTH = MOBILE_DEVICE_WIDTH;
  }

  componentDidMount() {
    this.initModels();
    this.initVideo();
    this.initCanvas();
    this.startMedia();
  }

  initModels = async () => {
    const faceModel = new FaceExtractor();
    await faceModel.load();

    const emotionModel = new EmotionNet();
    await emotionModel.load();

    this.models = { face: faceModel, emotion: emotionModel };
    this.setState({ ready: true });
  }

  initVideo = () => {
    this.video = document.createElement('video');
    this.video.autoplay = true;
    this.video.playsinline = true;
    this.video.style.display = 'none';
  }

  initCanvas = () => {
    this.canvas = document.querySelector('canvas');
    this.canvas.width = 0;
    this.canvas.height = 0;
  }

  startMedia = () => {
    const constraints = {
      audio: false,
      video: true
    };
    navigator.mediaDevices.getUserMedia(constraints)
      .then(this.handleSuccess)
      .catch(this.handleError);
  }

  handleSuccess = (stream) => {
    this.stream = stream; // èŽ·å–è§†é¢‘æµ
    this.video.srcObject = stream; // ä¼ ç»™ video
  }
  
  handleError = (error) => {
    console.log('navigator.getUserMedia error: ', error);
  }

  handleSnapshotClick = async () => {
    if (!this.state.ready) return;
    const canvas = this.canvas;
    const video = this.video;
    canvas.width = this.MOBILE_DEVICE_WIDTH;
    canvas.height = video.videoHeight * this.MOBILE_DEVICE_WIDTH / video.videoWidth;
    canvas.getContext('2d').drawImage(video, 0, 0, canvas.width, canvas.height);
    this.drawDetections();
    
    if (!this.isForwarding) {
      this.isForwarding = true;
      const imgSrc = await getImg(canvas.toDataURL('image/png'));
      this.analyzeFaces(imgSrc);
    }

    const that = this;
    setTimeout(() => {
      that.handleSnapshotClick();
    }, 10);
  }

  analyzeFaces = async (img) => {
    await nextFrame();

    if (!this.models) return;
    const faceResults = await this.models.face.findAndExtractFaces(img);
    const { detections, faces } = faceResults;

    // get emotion predictions
    let emotions = await Promise.all(
      faces.map(async face => await this.models.emotion.classify(face))
    );
    this.setState(
      { loading: false, detections, faces, emotions },
      () => {
        this.isForwarding = false;
      }
    );
  }

  drawDetections = () => {
    const { detections, emotions } = this.state;
    if (!detections.length) return;

    const { width, height } = this.canvas;

    const ctx = this.canvas.getContext('2d');
    const detectionsResized = detections.map(d => d.forSize(width, height));
    detectionsResized.forEach((det, i) => {
      const { x, y } = det.box
      const { emoji, name } = emotions[i][0].label;
      drawBox({ ctx, ...det.box, emoji });
      drawText({ ctx, x, y, text: emoji, name });
    });
  }

  render() {
    const { ready } = this.state;
    return (
      <div className="App">
        <div>{ ready ? 'å‡†å¤‡å°±ç»ª' : 'åŠ è½½æ¨¡åž‹...'}</div>
        <button className="btn" onClick={this.handleSnapshotClick}>Begin ~</button>
        <canvas></canvas>
      </div>
    );
  }
}

export default App;
